@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
    font-size: 2.5rem;
}

.color-1{border-color:#c26cd2 !important}.color-2{border-color:#1ec3c4 !important}.color-3{border-color:#fefe51 !important}.color-4{border-color:#f17ba5 !important}.color-1{border-color:#c26cd2}.shape-container{position:absolute;transition:all 0.5s ease-in-out}.shape{transform:scale(1);transform-origin:center;transition:transform 0.233s ease-in-out}.square{width:50px;height:50px;border:3px solid;border-radius:0;display:inline-block;position:absolute;background-color:#151515;z-index:1}.circle{width:25px;height:25px;border-radius:100%;border:3px solid;display:inline-block;background-color:#151515;z-index:1}.zig-zag{display:inline-block}.zig-zag:before,.zig-zag:after{width:15px;height:15px;border-left:3px solid;border-top:3px solid;border-color:inherit;border-radius:0 0 0 0;display:inline-block;content:'';transform:rotate(45deg);z-index:0}.zig-zag:after{margin-left:3px}.half-circle{width:24px;height:12px;border-top-left-radius:100px;border-top-right-radius:100px;background-color:#151515;border:4px solid;border-bottom:0;box-sizing:border-box;display:inline-block;z-index:0}.triangle{width:100px;height:48px;overflow:hidden;border-top:3px solid;border-radius:0 0 0 0;display:inline-block;z-index:1}.triangle:before{content:'';background-color:#151515;display:block;border-left:3px solid;border-bottom:3px solid;border-color:inherit;border-radius:0 0 0 0;height:67px;width:67px;transform:rotate(-45deg) translateX(50%) translateY(-14px)}.animate--jump{transform:scale(1.25) !important}@-webkit-keyframes jump{0%{transform:scale(1)}50%{transform:scale(1.1)}100%{transform:scale(1)}}@keyframes jump{0%{transform:scale(1)}50%{transform:scale(1.1)}100%{transform:scale(1)}}.topbar{position:absolute;top:0;left:0;width:100%;height:100px;box-sizing:border-box;padding:0 20px;display:flex;justify-content:flex-end;align-items:center}.topbar__nav-links{display:flex}.topbar__nav-links li:first-child button::after{background-color:#1ec3c4}.topbar__nav-links li:nth-child(2) button::after{background-color:#c26cd2}.topbar__nav-links li:nth-child(3) button::after{background-color:#fefe51}.topbar__nav-links li:last-child button::after{background-color:#f17ba5}.topbar__nav-links button{position:relative;display:flex;flex-direction:column;justify-content:center;align-items:center;border-left:0;overflow:hidden;border-right:0;border-top:0;z-index:1}.topbar__nav-links button span{position:relative;z-index:10}.topbar__nav-links button::after{content:'';position:absolute;left:0;bottom:0;width:100%;transform:scaleY(0);transform-origin:bottom;height:100%;z-index:0;transition:all 0.2s ease-in-out}.topbar__nav-links button:focus::after,.topbar__nav-links button:hover::after{transform:scaleY(0.2)}ul,li{list-style:none}.App{position:relative;display:flex;justify-content:center;width:100%;background-color:#151515;overflow:hidden}.app-inner{max-width:1200px;width:100%}.App-header{min-height:100vh;width:100%;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.main{display:flex;flex-direction:column;justify-content:center;align-items:center;padding:30px 50px;border:3px solid #c26cd2;background:#151515;border-radius:0;height:162px;min-width:506px;z-index:1;transition:all 0.466s ease-in-out}.main__home{height:162px}.main__graphics{height:300px}.main__web{height:400px}.main__contact{height:200px}.logo{width:500px;fill:white;z-index:3}@media (max-width: 768px){.logo{width:250px}}.background{position:fixed;left:0;top:0;width:100%;height:100vh;z-index:0}.btn{position:relative;background:#151515;border:3px solid #fefe51;color:white;min-width:90px;font-size:1rem;font-family:"Montserrat";font-weight:700;padding:15px;cursor:pointer;transition:all 0.2s ease;outline:0;z-index:5}.btn:hover{border-color:#f17ba5}

